
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapGetters } from 'vuex'

export default {
name: "Footer",
components: {
Tabbar,
TabbarItem,
Group,
Cell
},
data () {
return {
active1: false,
active2: false,
active3: false,
active4: false,
isLogin: "未登录",
}
},
created() {
if (this.$route.name === "首页") {
this.active1 = true;
}
else if (this.$route.name === "西瓜视频") {
this.active2 = true;
}
else if (this.$route.name === "微头条") {
this.active3 = true;
}
else {
this.active4 = true;
}
this.isLogin = (sessionStorage.getItem("userid")) ? "我的" : "未登录";
},
}



