import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/Layout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      redirect: '/Home',
      children: [
        {
          path: '/Home',
          name: '首页',
          component: () => import('../views/Home.vue'),
        },
        {
          path: '/Video',
          name: '西瓜视频',
          component: () => import('../views/Video.vue'),
        },
        {
          path: '/Headlines',
          name: '微头条',
          component: () => import('../views/Headlines.vue'),
        },
        {
          path: '/Account',
          name: '我的',
          component: () => import('../views/Account.vue'),
        },
      ]
    },
    {
      path: '/Notification',
      name: 'Notification',
      meta: { requiresAuth: true },
      component: () => import('../views/Notification.vue'),
    },
    {
      path: '/HeadlineMall',
      name: 'HeadlineMall',
      component: () => import('../views/HeadlineMall.vue'),
    },
    {
      path: '/SpecialOffer',
      name: 'SpecialOffer',
      component: () => import('../views/SpecialOffer.vue'),
    },
    {
      path: '/Feedback',
      name: 'Feedback',
      component: () => import('../views/Feedback.vue'),
    },
    {
      path: '/Setting',
      name: 'Setting',
      component: () => import('../views/Setting.vue'),
    },
    {
      path: '/Share',
      name: 'Share',
      component: () => import('../views/Share.vue'),
    },
  ]
})



