import Vue from 'vue'
import Vuex from 'vuex'
import home from './modules/home'
import getters from './getters'

Vue.use(Vuex)

const store = new Vuex.Store({
    modules: {
        home,
    },
    // 相当于store的计算属性
    getters,


    /*
        // state:状态树
        // Vuex 使用单一状态树(一个对象)包含全部的应用层级状态
        //
        //
        // 存储项目中的初始值
        // state: {
        //     count: 0
        // },
        // 只能在mutations中改变store中的状态
        // 调用mutations中的函数改变store的状态的方法： store.commit('函数名')
        //
        // store.commit('Count') 调用Count方法
        // mutations: {
        //     Count () {
        //         state.count++
        //         console.log(this.$store.state.count)
        //     }
        // },
    */

})

export default store


